module.exports = "export type RGBA = {\n  // all values are scaled between 0-1 instead of 0-255\n  r: number;\n  g: number;\n  b: number;\n  a: number; // opacity -- typically you should set this to 1.\n};\n\nexport type Header = {\n  frame_id: string;\n  stamp: Time;\n  seq: number;\n};\n\nexport type Point = {\n  x: number;\n  y: number;\n  z: number;\n};\n\nexport type Time = {\n  sec: number;\n  nsec: number;\n};\n\nexport type Translation = {\n  x: number;\n  y: number;\n  z: number;\n};\n\nexport type Rotation = {\n  x: number;\n  y: number;\n  z: number;\n  w: number;\n};\n\nexport type Pose = {\n  position: Point;\n  orientation: Quaternion;\n};\n\nexport type Quaternion = {\n  x: number;\n  y: number;\n  z: number;\n  w: number;\n};\n\nexport type Transform = {\n  header: Header;\n  child_frame_id: string;\n  transform: {\n    translation: Translation;\n    rotation: Rotation;\n  };\n};\n\nexport type PointStyle =\n  | \"circle\"\n  | \"cross\"\n  | \"crossRot\"\n  | \"dash\"\n  | \"line\"\n  | \"rect\"\n  | \"rectRounded\"\n  | \"rectRot\"\n  | \"star\"\n  | \"triangle\";\n\nexport type TwoDimensionalPlotLine = {\n  order?: number;\n  label: string;\n  backgroundColor?: string;\n  borderColor?: string;\n  borderDash?: number[];\n  borderWidth?: number;\n  pointBackgroundColor?: string;\n  pointBorderColor?: string;\n  pointBorderWidth?: number;\n  pointRadius?: number;\n  pointStyle?: PointStyle;\n  lineTension?: number;\n  data: { x: number; y: number }[];\n};\n/*\n * Use this type to plot values in the 2D Panel. If you need to change this\n * type, please ensure that is kept in sync with the TwoDimensionalPlot\n * counterpart.\n */\nexport type TwoDimensionalPlot = {\n  lines: TwoDimensionalPlotLine[];\n  points?: TwoDimensionalPlotLine[];\n  polygons?: TwoDimensionalPlotLine[];\n  title?: string;\n  yAxisLabel?: string;\n  xAxisLabel?: string;\n  gridColor?: string;\n};\n"