space;\n  flex: 1 1 auto;\n  display: flex;\n  position: relative;\n}\n\n.filtersBar {\n  background-color: $dark3;\n  display: flex;\n  flex-wrap: wrap;\n  flex: 1 1 auto;\n  left: 0;\n  width: 100%;\n}\n\n.severityFilter {\n  flex: 0 1 auto;\n  min-width: 120px;\n  margin-left: 10px;\n  width: unset !important;\n}\n\n.nodeFilter {\n  flex: 2 1 auto;\n  min-width: 200px;\n  width: unset !important;\n}\n\n.itemsCountField {\n  display: flex;\n  align-items: center;\n  border-radius: 4px;\n  color: rgba(255, 255, 255, 0.5);\n  padding: 0px 8px;\n}\n\n:global {\n  div.Select-menu-outer {\n    @extend %floating-box;\n  }\n  div.Select-menu {\n    padding-bottom: 5em !important; // TODO(jacob): not working when there are enough items to scroll?\n  }\n  .Select-control {\n    font-size: $option-font-size;\n    .Select-clear {\n      line-height: $option-font-size * 2;\n      font-size: $option-font-size * 1.5;\n    }\n  }\n  $select-item-font-size: $option-font-size;\n  $select-input-bg: $dark3;\n  $select-input-border-color: transparent;\n  $select-input-border-focus: transparent;\n  $select-input-height: $option-height * 1px;\n  $select-input-internal-height: 26px;\n  $select-text-color: $text-control;\n  $select-input-placeholder: transparentize($text-control, 0.5);\n  $select-option-bg: red;\n  $select-clear-color: $text-control;\n  $select-loading-color: $text-control;\n  $select-arrow-color: $text-control;\n\n  @import \"~react-select/scss/default\";\n  @import \"~react-virtualized/styles.css\";\n\n  .VirtualizedSelectOption {\n    @extend %menu-item;\n  }\n  .VirtualizedSelectSelectedOption {\n    @extend %menu-item-active;\n  }\n  .VirtualizedSelectFocusedOption {\n    @extend %menu-item-hover;\n    // TODO(jacob): doesn't override react-virtualized-select styles for some reason\n    background-color: $dark4 !important;\n  }\n  .Select {\n    width: 100%;\n  }\n  .Select-input > input {\n    color: $select-text-color;\n  }\n  .Select-multi-value-wrapper {\n    display: table-cell;\n    position: relative;\n  }\n  .Select--multi .Select-multi-value-wrapper {\n    width: 100%;\n  }\n  .Select--multi .Select-arrow {\n    opacity: 0.5;\n  }\n  .Select--multi .Select-value {\n    color: $text-bright;\n    background-color: $dark5;\n    border: 1px solid $background-control;\n    margin: 2px;\n  }\n  .Select--multi .Select-value-icon {\n    display: inline-flex;\n    align-items: center;\n    padding: 0 0 0 4px;\n    opacity: 0.5;\n    border: 0;\n    &:hover {\n      color: inherit;\n      background-color: transparent;\n      opacity: 1;\n    }\n  }\n}\n","//  Copyright (c) 2018-present, Cruise LLC\n//\n//  This source code is licensed under the Apache License, Version 2.0,\n//  found in the LICENSE file in the root directory of this source tree.\n//  You may not use this file except in compliance with the License.\n\n@import \"./colors.module.scss\";\n\n$padding: 8px;\n$text-size: 12px;\n$control-padding: 8px 8px 8px 16px;\n$control-margin: 0 0.2em;\n\n%bold {\n  font-weight: bold;\n  letter-spacing: 0.4px;\n}\n\n%floating-box-close-icon {\n  position: absolute;\n  right: 7px;\n  top: 7px;\n  cursor: pointer;\n  font-size: 16px;\n}\n\n%floating-box {\n  background-color: $dark3;\n  border-radius: 4px;\n  padding: 0;\n  box-shadow: 0 0px 32px rgba(8, 8, 10, 0.6);\n  overflow: hidden;\n  pointer-events: auto;\n  flex-shrink: 0;\n}\n\n%menu-item {\n  text-decoration: none;\n  padding: 8px 16px;\n  cursor: pointer;\n  color: $text-control;\n  user-select: none;\n  display: flex;\n  align-items: center;\n\n  &:hover {\n    @extend %menu-item-hover;\n  }\n}\n\n%modal-subtitle {\n  color: $text-bright;\n  font-size: 16px;\n  line-height: 16px * 1.25;\n}\n\n%clearfix {\n  &:after {\n    content: \" \";\n    visibility: hidden;\n    display: block;\n    height: 0;\n    clear: both;\n  }\n}\n\n%menu-item-active,\n%menu-item-hover {\n  color: rgba(255, 255, 255, 1);\n  background-color: $dark5;\n}\n\n$sans-serif-font: \"Inter UI\", -appl