module.exports = "import { Header, Pose, Point, RGBA, Time } from \"./types\";\n\nexport interface IMarker {\n  header: Header;\n  ns: string;\n  id: number;\n  type: number;\n  action: number;\n  pose: Pose;\n  scale: Point;\n  color: RGBA;\n  lifetime: Time;\n  frame_locked: boolean;\n  points: Point[];\n  colors: RGBA[];\n  text: string;\n  mesh_resource: string;\n  mesh_use_embedded_materials: boolean;\n}\n\n/**\n * Use this class to instantiate marker-like objects with defaulted values.\n */\nexport class Marker implements IMarker {\n  header: Header = {\n    frame_id: \"\",\n    stamp: {\n      sec: 0,\n      nsec: 0,\n    },\n    seq: 0,\n  };\n  ns: string = \"\";\n  id: number = 0;\n  type: number = 0;\n  action: number = 0;\n  pose: Pose = {\n    position: {\n      x: 0,\n      y: 0,\n      z: 0,\n    },\n    orientation: {\n      x: 0,\n      y: 0,\n      z: 0,\n      w: 0,\n    },\n  };\n  scale: Point = {\n    x: 0,\n    y: 0,\n    z: 0,\n  };\n  color: RGBA = { r: 0, g: 0, b: 0, a: 0 };\n  lifetime: Time = { sec: 0, nsec: 0 };\n  frame_locked: boolean = false;\n  points: Point[] = [];\n  colors: RGBA[] = [];\n  text: string = \"\";\n  mesh_resource: string = \"\";\n  mesh_use_embedded_materials: boolean = false;\n\n  constructor({\n    header,\n    ns,\n    id,\n    type,\n    action,\n    pose,\n    scale,\n    color,\n    lifetime,\n    frame_locked,\n    points,\n    colors,\n    text,\n    mesh_resource,\n    mesh_use_embedded_materials,\n  }: Partial<IMarker>) {\n    this.header = header || {\n      frame_id: \"\",\n      stamp: {\n        sec: 0,\n        nsec: 0,\n      },\n      seq: 0,\n    };\n    this.ns = ns || \"\";\n    this.id = id || 0;\n    this.type = type || 0;\n    this.action = action || 0;\n    this.pose = pose || {\n      position: {\n        x: 0,\n        y: 0,\n        z: 0,\n      },\n      orientation: {\n        x: 0,\n        y: 0,\n        z: 0,\n        w: 0,\n      },\n    };\n    this.scale = scale || { x: 0, y: 0, z: 0 };\n    this.color = color || { r: 0, g: 0, b: 0, a: 0 };\n    this.lifetime = lifetime || { sec: 0, nsec: 0 };\n    this.frame_locked = frame_locked || false;\n    this.points = points || [];\n    this.colors = colors || [];\n    this.text = text || \"\";\n    this.mesh_resource = mesh_resource || \"\";\n    this.mesh_use_embedded_materials = mesh_use_embedded_materials || false;\n  }\n}\n/**\n * Corresponds to the 'type' field of a marker.\n */\nexport enum MarkerTypes {\n  ARROW = 0,\n  CUBE = 1,\n  SPHERE = 2,\n  CYLINDER = 3,\n  LINE_STRIP = 4,\n  LINE_LIST = 5,\n  CUBE_LIST = 6,\n  SPHERE_LIST = 7,\n  POINTS = 8,\n  TEXT = 9,\n  MESH = 10,\n  TRIANGLE_LIST = 11,\n  FILLED_POLYGON = 107,\n}\n"